  </div>
</template>

<script>
export default {
  name: 'Strength'
}
</script>

<style scoped>
  .background {
    width: 1200px;
    height: 640px;
    margin: auto;
    position: relative;
  }

  .horizontal-solid {
    border: none;
    border-top: 1px solid #cccccc;
    height: 0;
  }

  .wpline-wrapper {
    margin: 15px 0;
    width: 100%;
    overflow: hidden;
  }

  .wp-title_content {
    font-size: 12px;
    /*line-height: 140%;*/
    overflow: hidden;
  }

  div img {
    width: 100%;
    height: 100%;
  }

</style>



