<script>
export default {
  name: 'ProductCenterNew',
  data () {
    return {
      leftClicked: false,
      leftFired: false,
      rightClicked: false,
      rightFired: false,
      currentIndex: 1,
      transactionEnd: false
    }
  },
  computed: {
    classOne () {
      return this.currentClass(1)
    },
    classTwo () {
      return this.currentClass(2)
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      window.setInterval(() => {
        this.left()
      }, 5000)
    },
    currentClass (index) {
      return {
        active: this.currentIndex === index,
        next: this.currentIndex !== index && this.leftClicked,
        prev: this.currentIndex !== index && this.rightClicked,
        left: this.leftFired,
        right: this.rightFired
      }
    },
    left () {
      this.leftClicked = true
      window.setTimeout(() => {
        this.leftFired = true
      }, 60)

      window.setTimeout(() => {
        if (this.currentIndex === 1) this.currentIndex = 2
        else this.currentIndex = 1
        this.leftClicked = false
        this.leftFired = false
      }, 600)
    },
    right () {
      this.rightClicked = true
      window.setTimeout(() => {
        this.rightFired = true
      }, 60)

      window.setTimeout(() => {
        if (this.currentIndex === 1) this.currentIndex = 2
        else this.currentIndex = 1
        this.rightClicked = false
        this.rightFired = false
      }, 600)
    }
  }
}
</script>

