  .single-page .welcome-box {
    margin-bottom: 40px;
    border-top: 1px solid #EBEBEB;
    padding: 31px 0 40px;
  }

  .single-page .welcome-box {
    margin-bottom: 40px;
  }

  .single-page .welcome-box h1 {
    margin-bottom: 5px;
  }

  .single-page #about-section .welcome-box {
    margin-bottom: 0px;
  }

  .single-page #about-section .services-box {
    margin-bottom: 50px;
  }

  .single-page #contact-section .welcome-box {
    margin-bottom: 30px;
  }

  .single-page footer {
    padding: 0;
  }

  .home-box {
    position: relative;
    overflow: hidden;
  }

  .home-box .background-view {
    position: absolute;
    width: 1920px;
    left: 50%;
    margin-left: -960px;
    overflow: hidden;
  }

  .slider-caption {
    position: absolute;
    height: 300px;
    top: 50%;
    margin-top: -100px;
    width: 100%;
    left: 0;
  }

  .slider-caption .flexslider {
    height: 85px;
    background: none;
    margin: 120px 0;
    background: rgba(0, 0, 0, 0.3);
  }

  .slider-caption .flex-direction-nav {
    display: none;
  }

  .slider-caption p.flex-caption {
    text-align: center;
    color: #fff;
    font-family: 'Open Sans', sans-serif;
    font-weight: 700;
    font-size: 50px;
    line-height: 80px;
  }

  .slider-caption p.flex-caption span {
    font-weight: 300;
  }

  .slider-caption .flex-control-nav {
    bottom: -80px;
  }

  ul.slide li.item {
    float: left;
  }
</style>



